ul.icons {
  list-style: none;
  padding: 0;
  margin-bottom: 30px;
  overflow: auto;

  & > li {
    float: left;
    width: 100px;
    height: 100px;
    border: 1px solid $grey-lighter;
    text-align: center;
    padding: 5px;
    margin-bottom: 5px;
    margin-right: 5px;
    font-size: 0.8em;

    .fa {
      font-size: 2.5em;
      padding: 15px; }
    div {
      display: block; } } }


@include breakpoint($sm) {
  ul.icons {
    margin-bottom: 15px;
    & > li {
      width: 60px;
      height: 60px;
      @include flex($align: center, $justify: center);
      .fa {
        font-size: 1.5em;
        padding: 0; }
      div {
        display: none; } } } }
